body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sad-hero {
  color: #737373;
  font-family: 'Exo 2';
}
.sad-outer {
  display: table;
  position: absolute;
  height: 100%;
  width: 100%;
}

.sad-middle {
  display: table-cell;
  vertical-align: middle;
}

.sad-inner {
  margin-left: auto;
  margin-right: auto;
  /*whatever width you want*/
}
.a-i {
    position: absolute;
    top: calc(50vh - 21px);
    left: calc(50vw - 101px);
    width: 200px;
    height: 40px;
    z-index: 1200;
    font: 700 20px Consolas,monaco,monospace;
    color: #00cc33;
    background: transparent;
    border: 2px solid #00cc33;
    padding: 0 10px;
}

.a-i-t {
    position: absolute;
    text-align: center;
    width: 200px;
    top: calc(50vh - 100px);
    left: calc(50vw - 89px);
    color: #00cc33;
    font: 700 20px Consolas,monaco,monospace;
}

input.a-i:focus {
    outline-width: 0;
}
