.a-i {
    position: absolute;
    top: calc(50vh - 21px);
    left: calc(50vw - 101px);
    width: 200px;
    height: 40px;
    z-index: 1200;
    font: 700 20px Consolas,monaco,monospace;
    color: #00cc33;
    background: transparent;
    border: 2px solid #00cc33;
    padding: 0 10px;
}

.a-i-t {
    position: absolute;
    text-align: center;
    width: 200px;
    top: calc(50vh - 100px);
    left: calc(50vw - 89px);
    color: #00cc33;
    font: 700 20px Consolas,monaco,monospace;
}

input.a-i:focus {
    outline-width: 0;
}